/* CSS Page for mouse and image styling */
/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.press-start-2p-regular {
  font-family: "Press Start 2P", system-ui;
  font-weight: 400;
  font-style: normal;
}

.containerStyle {
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 10%;
  pointer-events: none;
}
.resetButton {
  font-family: "Press Start 2P", system-ui;
  font-size: 20px;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  position: fixed;
  right: 20px; /* Adjust the value to set the desired distance from the right side */
  top: 10%;
  cursor: pointer;
}
.resetButton:hover {
  background-color: grey;
}
body {
  background-image: url('../public/background.png');
  margin: 0;
  min-height: 100vh;
}

img {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 50vmin;
  aspect-ratio: 3 / 2;
  object-fit: contain;
}

.mouseMove {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50vmin;
  aspect-ratio: 45 / 2;
  background-color: rgb(126, 146, 157);
  visibility: hidden;
}
